    url("leaf.jpg"); */
}

.sp-danmu-wrapper {
  display: flex;
  position: fixed;
  cursor: pointer;
  z-index: 2147483647;
}
.sp-danmu-avatar {
  /* display: inline-block; */
  border-radius: 100%;
  width: 35px;
  height: 35px;
  object-fit: cover;
  border: 1px solid lightgray;
}
.sp-danmu-content-text {
  border: 1px solid lightgray;
  background-color: white !important;
  color: black !important;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 20px;
  line-height: 25px;
  border-radius: 5px;
  max-width: 500px;
  margin-left: 5px;
  /* border: none; */
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
  /* box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.2); */
  text-align: left;
}
.sp-danmu-content-text.self {
  background-color: #bf0 !important;
  color: black !important;
}
.sp-danmu-content-img {
  width: 100px;
  height: 100px;
  margin-top: -10px;
}
.inline-emoji {
  /* font-size: 18px; */
  /* bottom: -3px; */
  /* position: relative; */
}

.sp-chatbox-iframe {
  /* don't remove anything, this is fighting other sites' styling*/
  min-height: 0px;
  min-width: 0px;
  height: 100%;
  width: 100%;
  max-width: 9999px;
  max-height: 9999px;
  border: none;
  /* put display: none here, but still need to set it in js
  so that we can read it easily later
  */
  margin-bottom: 0px;
}
.sp-chatbox-iframe-wrapper {
  position: fixed !important;
  /* left: 0; */
  bottom: 0;
  top: unset !important;
  z-index: 2147483646;
  /* one less than image modal */
  display: none;
  border: 1px solid lightgray;
  border-bottom: none;
  /* border-right: none; */
  /* box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.31); */
}
.sp-chatbox-drag-handle {
  margin-top: -20px;
  height: 20px;
}
.sp-chatbox-drag-handle center {
  display: none;
}
.sp-chatbox-drag-handle:hover center {
  display: inline-block;
}
.sp-blue-button {
  background-color: #2196f3 !important;
}
.sp-chat-icon-wrapper {
  z-index: 2147483647;
  margin: 10px;
  width: 32px;
  height: 32px;
  cursor: pointer;
  position: fixed;
  right: 0;
  bottom: 0;
  color: #2096f3;
  border: 1px solid #2096f3;
  border-radius: 100%;
  text-align: center;
  line-height: 32px;
}
.sp-chat-icon-wrapper svg {
  vertical-align: unset;
}
.sp-chat-icon-wrapper img {
  width: 100%;
  height: 100%;
  /* 
  inline style is display none so we don't show the icon
  until css file is properly loaded
   */
  display: block !important;
}

div.sp-image-modal {
  z-index: 2147483647;
  /* iframe is 2147483646; */
}

.sp-image-modal img {
  width: 100%;
}

